import Login from './views/Login.vue'
import Register from './views/Register.vue'
// import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import RecordCenter from './views/Record/RecordCenter.vue'
// import Form from './views/nav1/Form.vue'
// import user from './views/nav1/user.vue'
import Cteam from './views/asp/Cteam.vue'
import Bteam from './views/asp/Bteam.vue'
import Allenter from './views/asp/Allenter.vue'
import Blackenter from './views/asp/Blackenter.vue'
import Verify from './views/asp/Verify.vue'
import Enterdetail from './views/asp/Enterdetail.vue'
import Tidings from './views/Tidings/Tidings.vue'
import NewsMessage from './views/Tidings/NewsMessage.vue'
import MessageDetail from './views/Tidings/MessageDetail.vue'
import Enterprise from './views/asp/Enterprise.vue'
import Hotwords from './views/asp/Hotwords.vue'
import Carousel from './views/asp/Carousel.vue'
import Position from './views/asp/Position.vue'
import Suggestion from './views/asp/Suggestion.vue'
import Resume from './views/asp/Resume.vue'
import Cuser from './views/asp/Cuser.vue'
import ResumeReview from './views/asp/ResumeReview.vue'
import ResumeDetail from './views/asp/ResumeDetail.vue'
import JobAuait from './views/asp/JobAuait.vue'
import JobDetail from './views/asp/JobDetail.vue'
import CompanyInformation from './views/asp/CompanyInformation.vue'
import Qualifications from './views/asp/Qualifications.vue'
import BasicInformation from './views/asp/BasicInformation.vue'
import InformationVerify from './views/asp/InformationVerify.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/register',
        component: Register,
        name: '',
        hidden: true
    },
    {
        path: '/Verify',
        component: Verify,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        // hidden: true,
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/RecordCenter', iconCls: 'fa fa-id-card-o', component: RecordCenter, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        hidden: true,
        meta: {
            requireAuth: true // 
        },
        iconCls: 'fa fa-id-card-o',
        children: [
            {
                path: '/Cteam',
                iconCls: 'fa fa-id-card-o',
                redirect: '/Cteam/Cuser',
                component: Cteam,
                name: 'C',
                children: [
                    { path: '/Cteam/Cuser', iconCls: 'fa fa-address-card', component: Cuser, name: 'C' },
                    { path: '/Cteam/Resume', iconCls: 'fa fa-address-card', component: Resume, name: '', hidden: true },
                ]
            },
            {
                path: '/Bteam',
                component: Bteam,
                redirect: '/Bteam/Enterprise',
                iconCls: 'fa fa-address-card',
                name: '',
                children: [
                    { path: '/Bteam/Enterprise', iconCls: 'fa fa-address-card', component: Enterprise, name: '' },
                    { path: '/Bteam/Allenter', iconCls: 'fa fa-address-card', component: Allenter, name: '' },
                    { path: '/Bteam/Blackenter', iconCls: 'fa fa-address-card', component: Blackenter, name: '' },
                    { path: '/Bteam/Verify', iconCls: 'fa fa-address-card', component: Verify, name: '', hidden: true },
                    { path: '/Bteam/Enterdetail', iconCls: 'fa fa-address-card', component: Enterdetail, name: '', hidden: true },
                ]
            }
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'C',
        iconCls: 'fa fa-id-card-o',
        hidden: true,
        children: [
            { path: '/Hotwords', iconCls: 'fa fa-id-card-o', component: Hotwords, name: '' },
            { path: '/Carousel', iconCls: 'fa fa-id-card-o', component: Carousel, name: '' },
            { path: '/Position', iconCls: 'fa fa-id-card-o', component: Position, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        meta: {
            requireAuth: true // 
        },
        children: [
            {
                path: '/CompanyInformation',
                iconCls: 'fa fa-id-card-o',
                component: CompanyInformation,
                meta: {
                    requireAuth: true // 
                },
                name: '',
                children: [
                    {
                        path: '/CompanyInformation/Qualifications', iconCls: 'fa fa-address-card', component: Qualifications, meta: {
                            requireAuth: true, // 
                        }, name: ''
                    },
                    {
                        path: '/CompanyInformation/BasicInformation', iconCls: 'fa fa-address-card', meta: {
                            requireAuth: true // 
                        }, component: BasicInformation,
                        name: ''
                    },
                    {
                        path: '/CompanyInformation/InformationVerify', iconCls: 'fa fa-address-card', meta: {
                            requireAuth: true // 
                        }, component: InformationVerify, name: '', hidden: true
                    },
                    {
                        path: '/CompanyInformation/Verify', iconCls: 'fa fa-address-card', meta: {
                            requireAuth: true // 
                        }, component: Verify, name: '', hidden: true
                    },

                ]
            },
            {
                path: '/JobAuait', iconCls: 'fa fa-id-card-o', meta: {
                    requireAuth: true // 
                }, component: JobAuait, name: ''
            },
            {
                path: '/ResumeReview', iconCls: 'fa fa-id-card-o', meta: {
                    requireAuth: true // 
                }, component: ResumeReview, name: '', children: [

                ]
            },
            {
                path: '/ResumeDetail', iconCls: 'fa fa-id-card-o', meta: {
                    requireAuth: true // 
                }, component: ResumeDetail, name: '', hidden: true
            },
            {
                path: '/JobDetail', iconCls: 'fa fa-id-card-o', meta: {
                    requireAuth: true // 
                }, component: JobDetail, name: '', hidden: true
            },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        hidden: true,
        children: [
            { path: '/Suggestion', iconCls: 'fa fa-id-card-o', component: Suggestion, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        meta: {
            requireAuth: true // 
        },
        children: [
            {
              path: '/Tidings', iconCls: 'fa fa-id-card-o', meta: { requireAuth: true }, component: Tidings, name: ''},
            { path: '/NewsMessage', iconCls: 'fa fa-id-card-o', component: NewsMessage, name: '', hidden: true },
            { path: '/MessageDetail', iconCls: 'fa fa-id-card-o', component: MessageDetail, name: '', hidden: true },
        ]
    },
    // {
    //     path: '/',
    //     component: Tidings,
    //     name: '',
    //     iconCls: 'fa fa-address-card',
    //     leaf: true,//
    //     children: [
    //         { path: '/page6', component: Page6, name: '' }
    //     ]
    // },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;



